@font-face {
  font-family: alrightSans;
  src: url(./alright.otf);
}

.name {
  font-size: 90px;
  font-weight: 100;
  margin: 0;
}

.container {
  width: 1180px;
  margin: 50px auto;
}

.bio {
  display: inline-block;
}

.intro {
  margin: 0;
  text-align: left;
}

.app {
  font-family: alrightSans;
  margin: 0;
}

.project-container {
  margin-top: 300px;
}

.project-container-title-container {
  background-color: #FFFDE7;
}

.project-container-title {
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-weight: 400;
  font-size: 40px;
  margin: 0;
}

.project-list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.project-list-item {
  width: 45%;
  float: left;
  margin: 5% 5% 0 0;
}




